import React, { Component } from 'react';
import { Button, Menu, Dropdown, Icon, Drawer, Input } from 'antd';
import Router from 'next/router'

import { connect } from 'react-redux'
import '../../static/css/app.css'
import Axios from 'axios';


const host = HOST.host;
let { Search } = Input;

const menu = (
    <Menu>
        <Menu.Item key="0">
            <a href="http://www.alipay.com/">1st menu item</a>
        </Menu.Item>
        <Menu.Item key="1">
            <a href="http://www.taobao.com/">2nd menu item</a>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="3" >退出登录</Menu.Item>
    </Menu>
);


class Navinfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            login: false,
            drawer: false,
            searchlist: []
        };
    }

    componentDidMount() {
        console.log(this.props)
        if (this.props.auth) {
            this.setState({
                login: true
            })
        }
    }

    LinkPages = (href) => {

        Router.push(href)
    }

    onClose = (e) => {
        this.setState({
            drawer: false
        })
    }

    showDrawer = (e) => {
        this.setState({
            drawer: true
        })
    }

    searchList = (e) => {
        Axios.get(host+'/public/search',{
            params: {
              ID: 12345
            }
          })
          .then(function (response) {
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          })
        console.log(e)
    }
    
    loginout = e =>{
        this.props.LoginOut()
        this.setState({
            login: false
        })

    }
    render() {
        let { login } = this.state;
        return (
            <div className="navinfo">
                <Button shape="circle" icon="search" onClick={this.showDrawer} />
                <Button>写推文</Button>
                {
                    login ? (<Dropdown overlay={(
                        <Menu>
                            <Menu.Item key="0">
                                <a href="http://www.alipay.com/">1st menu item</a>
                            </Menu.Item>
                            <Menu.Item key="1">
                                <a href="http://www.taobao.com/">2nd menu item</a>
                            </Menu.Item>
                            <Menu.Divider />
                            <Menu.Item key="3" onClick={this.loginout}>退出登录</Menu.Item>
                        </Menu>
                    )} trigger={['click']}>

                        <img src="/static/img/123.jpg" className="avater" />
                    </Dropdown>) : <div className="unlogin">
                            <Button type="link" size={"small"} onClick={this.LinkPages.bind(this, '/login')}>
                                登录
                    </Button>
                            <Button type="link" size={"small"} onClick={this.LinkPages.bind(this, '/login')}>
                                注册
                    </Button>
                        </div>
                }

                <Drawer
                    title={<Search
                        placeholder="搜索更多内容"
                        onSearch={this.searchList}
                        style={{ width: 200 }}
                    />}
                    placement="bottom"
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.drawer}
                >
                    {
                        this.state.searchlist.map((value, i) => {
                            return (<p key={i}><a href={'/content/' + value._id} target="_blank" rel="noopener noreferrer">{value.title}</a></p>)
                        })
                    }
                </Drawer>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return state
}
//需要触发什么行为
function mapDispatchToProps(dispatch) {
    return {
        ToggleLogin: (user) => dispatch({ type: 'toggleAuth', user }),
        LoginOut: () => dispatch({ type: 'loginOut' })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navinfo);